import React, { useState } from 'react';
import clsx from 'clsx';
import { Drawer, IconButton, List, ListItem, Slider } from '@material-ui/core';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { AppBar, Toolbar } from '@material-ui/core';
import { ListItemText, ListItemIcon } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/Close';
import Divider from '@material-ui/core/Divider';

interface SideMenuProps {
  parts: string[];
  currentPart: string;
  changeCurrentPart: (val:string) => void;
  speechSpeedRate: number;
  changeSpeechSpeedRate: (val:number) => void;
}

const SideMenu: React.FC<SideMenuProps> = props => {
  const [menuToggle, setMenuToggle] = useState(false);

  const toggleDrawer = () => {
    setMenuToggle(!menuToggle);
  };

  const parts = props.parts;
  const drawerWidth = '50vw';

  const useStyles = makeStyles((theme: Theme) => 
    createStyles({
      header: {
        minHeight:'4vh',
        backgroundColor: '#7A7A7A',
        marginBottom: '1vh'
      },
      open: {
        width: drawerWidth,
      },
      close: {
        width:'100%',
      },
      closeIcon: {
        marginTop: '16px',
        marginLeft: '16px',
      },
      hide: {
        display: 'none',
      },
      list: {
        width: drawerWidth,
      }
    })
  );

  const classes = useStyles();

  return ( 
    <>
      <Toolbar
        className={`${menuToggle ? classes.open : classes.close} ${classes.header}` }
      >
        <Typography
          variant="h6"
          noWrap
          style={{flexGrow: 1, textAlign:'center'}}
        >
          {props.currentPart}    
        </Typography>
        <IconButton
          color="inherit"
          className={menuToggle ? classes.hide : ''}
          aria-label="open drawer"
          edge="end"
          onClick={() => toggleDrawer()}
        >
          <MenuIcon />
        </IconButton>
      </Toolbar>
      <Drawer
        anchor='right'
        open={menuToggle}
        onClose={() => setMenuToggle(false)}
      >
        <div className={classes.list}>
          <CloseIcon
            className={classes.closeIcon}
            onClick={() => setMenuToggle(false)}
          />
          <List component="nav" aria-label="secondary part">
            {
              parts.map(p => {
                return (
                  <ListItem 
                    button
                    onClick={() => {
                      props.changeCurrentPart(p);
                      setMenuToggle(false);
                    }}
                    key={p}
                  >
                    <ListItemText primary={p}/>
                    <ListItemIcon>
                      {
                        p === props.currentPart
                        &&
                        <CheckIcon />
                      }
                    </ListItemIcon>
                  </ListItem>
                )
              })
            }
            <ListItem
              button
              onClick={() => {
                props.changeCurrentPart('favorite');
                setMenuToggle(false);
              }}
              key={'favorite'}
            >
              <ListItemText primary="favorite"/>
            </ListItem>
          </List>
          <Divider />
          <List component="nav" aria-label="thirdry speechSpeed">
            <ListItem>
              <ListItemText primary="voice speed"/>
              <Slider
                min={0.1}
                max={1}
                step={0.1}
                value={props.speechSpeedRate}
                aria-labelledby="speech-speed-rate"
                valueLabelDisplay="auto"
                onChange={(e, v) => {
                  let rate:number = props.speechSpeedRate;
                  if (typeof(v) ===  'number') {
                    rate = v;
                  }
                  props.changeSpeechSpeedRate(rate);
                }}
              />
            </ListItem>
          </List>
        </div>
      </Drawer>
    </>
  );
}

export default SideMenu;